import React, { useState } from 'react';
import Equinos from "./equinos/Equinos.jsx"
import Propietarios from "./equinos/Propietarios.jsx"
import MarketBar from "./general/statistic_bar.jsx"

function Dashboard() {
  const [currentRoute, setCurrentRoute] = useState('/market');

  const handleNavClick = (route) => {
    setCurrentRoute(route);
  }

  let componentToRender;
  switch(currentRoute) {
    case '/Stequinos':
     componentToRender = <Equinos ClassName="active"/>;
      break;
    case 'Stpropietarios':
      componentToRender = <Propietarios ClassName="active"/>;
      break;
    // case '/implementos':
    //   componentToRender = <NftImplementos ClassName="active"/>;
    //   break;
    default:
      componentToRender = <Equinos ClassName="active"/>;
      break;
  }

  return (
    <div>
      <MarketBar handleNavClick={handleNavClick}/>
     
      {componentToRender}
    </div>
  )
}

export default Dashboard;

