import React, { useState } from 'react';
import axios from 'axios';
import { linkApp } from './../../../../generals/configuracion/variablesPublicas.jsx';
function EquineSearch() {
  const [equineId, setEquineId] = useState('');
  const [equineData, setEquineData] = useState(null);
  
  const handleSearch = () => {
    // Verificar que el ID del caballo sea un nmero vlido entre 1 y 999999
    if (!/^\d{1,6}$/.test(equineId)) {
      alert('Por favor, introduce un nmero vlido entre 1 y 999999.');
      return;
    }

    // Crear la URL de la solicitud con el ID del caballo
    const url = `${linkApp}/equino?equineId=${equineId}`;

    // Realizar la solicitud HTTP usando Axios
    axios.get(url)
      .then(response => {
        const data = response.data;
        if (data.length > 0) {
          // Si se encuentran datos, actualizar el estado con la informacin del caballo
          setEquineData(data);
        } else {
          // Si no se encuentra ningn dato, mostrar un mensaje de error
          alert('No se encontraron datos para el caballo con el ID proporcionado.');
        }
      })
      .catch(error => {
        console.error('Error en la solicitud:', error);
        alert('Se produjo un error al cargar los datos del caballo. Por favor, intntalo de nuevo ms tarde.');
      });
  };

  return (
    <div>
      <div>
        <label htmlFor="equineId">ID del Caballo:</label>
        <input
          type="number"
          id="equineId"
          value={equineId}
          onChange={(e) => setEquineId(e.target.value)}
        />
        <button onClick={handleSearch}>Buscar</button>
      </div>
      {equineData && (
        <div>
          <h2>Informacin del Caballo</h2>
          {/* Mostrar la informacin del caballo aqu */}
          <pre>{JSON.stringify(equineData, null, 2)}</pre>
        </div>
      )}
    </div>
  );
}

export default EquineSearch;
